.container {
    width: 320px;
    padding-left: 20px;
    padding-right: 20px;
    margin-left: auto;
    margin-right: auto;
}
@media screen and (min-width:480px) {
    .container {
        width: 480px;
    }
}
@media screen and (min-width:768px) {
    .container {
        width: 768px;
    }
}
@media screen and (min-width:1280px) {
    .container {
        width: 1280px;
    }
}